import React from "react";
import { IAuthor, IChildImageSharpResult } from "../../interfaces";
import Img from 'gatsby-image';
import { HTMLContent } from "../Content/Content";
import "./Author.sass";
import { Link } from "gatsby";
import { OutboundLink } from "react-ga";
import { MyOutboundLink } from "../Misc";

export class AuthorContent extends React.Component<{author: IAuthor, avatar: IChildImageSharpResult, badge: IChildImageSharpResult}>
{
  render() 
  {
    return(
      <div className="author-content">
        <div className="columns">
          <div className="author-badge-container column is-2">
            <Link to={"/author/"+this.props.author.frontmatter.slug}><Img fixed={this.props.avatar.childImageSharp.fixed}></Img></Link>
            {
              this.props.badge != null ? (
                <div className="author-badge">
                  <MyOutboundLink href="https://www.youracclaim.com/badges/ce2fe392-188d-4c28-bf5f-1c715de2b131/public_url">
                    <Img fixed={this.props.badge.childImageSharp.fixed}></Img>
                  </MyOutboundLink>
                </div>
              ): ""
            }
          </div>
          <div className="column">
            <div>
              <Link to={"/author/"+this.props.author.frontmatter.slug} className="author-headline">{this.props.author.frontmatter.firstname + " " + this.props.author.frontmatter.lastname}</Link>
            </div>
            <div>
              <HTMLContent content={this.props.author.html}></HTMLContent>
            </div>
          </div>
        </div>
      </div>
    );
  }
}