import React, { ChangeEvent } from "react";
import "./CookieWarning.sass";
import { Link } from 'gatsby';
import { CookieService } from '../../services/CookieService';
import { config } from "../../config";
import { AppInsightsService } from '../../services/AppInsightsService';
import { IDisposable } from '../../common/MySubject';


type IState = {
  cookieWarningDismissed: boolean, 
  lang: "de"|"en",
  enableAzureAppInsights: boolean,
  enableGoogleAnalytics: boolean,
}

export class CookieWarning extends React.Component<{}, IState>
{
  sub: IDisposable;
  
  constructor(props = null)
  {
    super(props);
    this.state = {
      cookieWarningDismissed: true,
      lang: "de",
      enableAzureAppInsights: false,
      enableGoogleAnalytics: false
    }
  }
  
  componentDidMount()
  {
    this.setState({
      cookieWarningDismissed: CookieService.cookies.cookieWarningDismissed2 == "true"
    })
    
    this.sub = CookieService.$cookies.subscribe((cookies) => {
      this.setState({
        cookieWarningDismissed: cookies.cookieWarningDismissed2 == "true",
        enableAzureAppInsights: cookies.enableAzureAppInsights == "true",
        enableGoogleAnalytics: cookies.enableGoogleAnalytics == "true"
      })
    })
  }
  
  componentWillUnmount()
  {
    this.sub.dispose();
  }
  
  on_changeLangButton_clicked()
  {
    this.setState({
      lang: this.state.lang == "de" ? "en": "de"
    });
  }

  on_saveButton_clicked()
  {
    CookieService.setCookies({
      "cookieWarningDismissed2": "true",
      "enableAzureAppInsights": this.state.enableAzureAppInsights.toString() as any,
      "enableGoogleAnalytics": this.state.enableGoogleAnalytics.toString() as any,
    });
  }
  
  on_allCookiesButton_clicked()
  {
    this.setState({
      enableAzureAppInsights: true,
      enableGoogleAnalytics: true
    });
    setTimeout(() => {
      this.setState({
        cookieWarningDismissed: true
      });
      CookieService.setCookies({
        "cookieWarningDismissed2": "true",
        "enableAzureAppInsights": this.state.enableAzureAppInsights.toString() as any,
        "enableGoogleAnalytics": this.state.enableGoogleAnalytics.toString() as any,
      });
    }, 200);
  }
  
  on_optsChanged = (ev: ChangeEvent) =>
  {
    let t = ev.target as HTMLInputElement;
    let s = {};
    s[t.name] = t.checked;
    this.setState(s);
  }

  render()
  {
    let show = typeof window !== "undefined" ? location.pathname.indexOf("datenschutz") < 0 : true;
    if(this.state.cookieWarningDismissed == true || show == false)
    {
      return (
        <>
        </>
      )
    }
    else 
    {
      return (
        <div className="cookie-warning-container">
          <div className="cookie-warning">
            <div className="cookie-header">
              <h3>Cookie Einstellungen</h3>
            </div>
            <div className="cookie-text">
              {
                this.state.lang == "de" ? "Diese website verwendet cookies. Bitte wählen Sie ihre bevorzugte Cookie Einstellung." : "This website uses cookies. Please select your preferred cookie configuration."
              }
              <Link style={{marginLeft: "5px"}} to="/datenschutz">
              {
                this.state.lang == "de" ? "Erfahre mehr.": "Learn more."
              }
              </Link>
            </div>
            <div className="cookie-options">
              <div className="desktop">
                <div className="field">
                  <input type="checkbox" className="switch is-rounded" id="necessaryCookies" disabled={true} checked={true} title={this.state.lang == "de" ? "Diese cookies werden benötigt um die Seite zu verwenden" : "These cookies are necessary to use this website"}/>
                  <label htmlFor="necessaryCookies">
                  {
                    this.state.lang == "de" ? "Notwendige cookies" : "Essential cookies"
                  }
                  </label>
                </div>
                <div className="field">
                  <input type="checkbox" className="switch is-rounded" name="enableGoogleAnalytics" id="enableGoogleAnalytics" checked={this.state.enableGoogleAnalytics} onChange={this.on_optsChanged} />
                  <label htmlFor="enableGoogleAnalytics">Google analytics</label>
                </div>
                <div className="field">
                  <input type="checkbox" className="switch is-rounded" name="enableAzureAppInsights" id="enableAzureAppInsights" checked={this.state.enableAzureAppInsights} onChange={this.on_optsChanged} />
                  <label htmlFor="enableAzureAppInsights">Azure App-Insights</label>
                </div>
              </div>
              <div className="mobile">
                <div className="field">
                    <input type="checkbox" className="switch is-rounded is-medium" id="necessaryCookies" disabled={true} checked={true} title={this.state.lang == "de" ? "Diese cookies werden benötigt um die Seite zu verwenden" : "These cookies are necessary to use this website"}/>
                    <label htmlFor="necessaryCookies">
                    {
                      this.state.lang == "de" ? "Notwendige cookies" : "Essential cookies"
                    }
                    </label>
                  </div>
                  <div className="field">
                    <input type="checkbox" className="switch is-rounded is-medium" name="enableGoogleAnalytics" id="enableGoogleAnalytics" checked={this.state.enableGoogleAnalytics} onChange={this.on_optsChanged} />
                    <label htmlFor="enableGoogleAnalytics">Google analytics</label>
                  </div>
                  <div className="field">
                    <input type="checkbox" className="switch is-rounded is-medium" name="enableAzureAppInsights" id="enableAzureAppInsights" checked={this.state.enableAzureAppInsights} onChange={this.on_optsChanged} />
                    <label htmlFor="enableAzureAppInsights">Azure App-Insights</label>
                  </div>
              </div>
              <div className="cookie-buttons">
                <div>
                  <button onClick={this.on_allCookiesButton_clicked.bind(this)} className="button is-small action-button">
                    {
                      this.state.lang == "de" ? "Alle auswählen" : "Select all"
                    }
                  </button>
                  <button onClick={this.on_saveButton_clicked.bind(this)} className="button save-button is-small">
                    {
                      this.state.lang == "de" ? "Speichern" : "Save"
                    }
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )
    }
  }
}
