import React from "react";
import { ICustomerStory, IGatsbyLocation } from '../../interfaces';
import "./CustomerStory.sass"
import "../Content/Content.sass"
import Img from 'gatsby-image';
import { Link } from "@reach/router";
import { FaLongArrowAltRight } from "react-icons/fa";
import  { MyOutboundLink } from "../Misc";

export class CustomerStory extends React.Component<{story: ICustomerStory, location: IGatsbyLocation}>
{
  render()
  {
    let story = this.props.story.fields.story;
    return (
      <div>
        <div className="customerstory-container">
          <div className="customerstory-icon">
            <MyOutboundLink href={story.url} rel="nofollow" className="customerstory-company" eventLabel="story" target="_blank">
              <Img fixed={this.props.story.childImageSharp.fixed} className={story.className}></Img>
            </MyOutboundLink>
          </div>
          <div className="customerstory-text">
            <div>
              <h3><MyOutboundLink href={story.url} rel="nofollow" className="customerstory-company" target="_blank">{story.company}</MyOutboundLink></h3>
            </div>
            <span>
              <p>{story.text}</p>
            </span>
            <span>
              <span className="customerstory-author">{story.author}</span>
              <span className="customerstory-position"> ({story.position}</span>
              {
                story.country != null ? <span className="customerstory-country">, {story.country}</span> : ""
              }
              <span className="customerstory-author">)</span>
            </span>
          </div>
          {
            this.props.location.pathname.replace(/\//g, "").toUpperCase() != "KUNDENMEINUNGEN" ? (
            <div className="home-customerstory-button-container">
              <Link  to="/kundenmeinungen">
                <FaLongArrowAltRight/>mehr Kundenmeinungen
              </Link>
            </div>
            ) : ""
          }
        </div>
      </div>
    )
  }
}
