import { ApplicationInsights } from '@microsoft/applicationinsights-web';
import { environment } from '../environment';
import { CookieService } from './CookieService';

class AppInsightsServiceClass
{
  private appInsights: ApplicationInsights;
  private readonly customEventsVersion = 2;
  private initialized = false;
  
  constructor()
  {
    console.log(process.env.NODE_ENV);
    if(typeof window !== "undefined")
    {
      this.appInsights = new ApplicationInsights({
        config: {
          instrumentationKey: "55e88bf4-37a9-460c-8987-fb67016be741",
          disableFetchTracking: true
        }
      });
      CookieService.$cookies.subscribe(c => {
        if(c.enableAzureAppInsights == "true")
        {
          if(this.initialized == false)
          {
            this.appInsights.loadAppInsights(); 
            this.initialized = true;
            console.log("Initialized app insights");
          }
        }
        else
        {
          console.log("Disabled app insights");
          this.appInsights.config.disableTelemetry = true;
        }
      });
    }
  }
  
  trackError(message: string, error: Error)
  {
    if(environment.isDev == true)
      return;
    this.appInsights.trackException({
      error,
      properties: {
        message,
        version: this.customEventsVersion
      }
    });
  }
}


export const AppInsightsService = new AppInsightsServiceClass();
