import React from "react";
import { HTMLContent } from './Content';
import "./Content.sass"
import { createGuid } from '../../../import/src/misc';
import { Tooltip } from "../../common/Misc";
import { FaCopy } from "react-icons/fa";

const parser = typeof window != "undefined" ? new DOMParser() : null;

interface ITab
{
  formatted: string;
  label: string;
  tooltip: string;
}

interface ICodeBlockProps
{
  formatted: string;
  footerhtml?: string
  language?: string;
  usetabs?: string;
}

export class CodeBlock extends React.Component<ICodeBlockProps, { formatted: string, daxGerman: boolean, currTab: number }>
{
  code: string = ""
  tabs: ITab[];
  
  constructor(props)
  {
    super(props);
    
    let formatted = Buffer.from(this.props.formatted, 'base64').toString('utf8');
    let f: string;
    if(this.props.usetabs == "true")
    {
      this.tabs = JSON.parse(formatted);
      f = this.tabs[0].formatted;
    }
    else
    {
      f = formatted;
    }
    
    this.state = {
      formatted: f,
      daxGerman: false,
      currTab: 0
    }
  }
  
  on_copy_clicked = () => {
    navigator.clipboard.writeText(this.code);
  }
  
  on_changeMode = () => {
  }
  
  onTabChanged = (i: number, ev) => {
    this.setState({
      currTab: i,
      formatted: this.tabs[i].formatted
    });
    ev.preventDefault();
  }
  
  render() {
    let footerHtml = this.props.footerhtml != null ? Buffer.from(this.props.footerhtml, 'base64').toString('utf8') : null;
    let lines      = [];
    if(typeof window !== "undefined")
    {
      lines = this.state.formatted.split(/<\s*br\s*\/?\s*>/g)
        .filter(l => {
          let n =  parser.parseFromString(`<span id="mark">${l}</span>`, "text/html").getElementById("mark")
          if(n != null && n.textContent != "")
          {
            this.code += n.textContent.replace(/\n/g, "").trimRight() + "\n"
          }
          return n.firstChild != null && n.textContent != ""
        })
        .filter(l => l.trim() != "")
        .map(l => l.replace(/^(\s*)/, "<span style='white-space: pre'>$1</span>"))
    }
    
    let guid = createGuid();
    
    return (
      <div className="code-block-container">
        <div className="code-block-table-container">
          <table>
            <tbody>
              <tr>
                <td className="line-numbers">
                  {lines.map((l,i) => <div className={`line-numbers code-line ${i%2 == 0 ? "code-line-1" : "code-line-2"}`} key={i}>{i+1}</div>)}
                </td>
                <td>
                  <div style={{whiteSpace: "pre"}}>
                    {lines.map((l,i) => <HTMLContent key={i} className={`code-line ${i%2 == 0 ? "code-line-1" : "code-line-2"}`} content={l}/>)}
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="code-block-footer">
          <div className="left">
          {/* {
            this.props.language == "dax" ? (
              <Tooltip text={<span>Für deutsches Excel formattieren <br/>(Semicolon statt Komma).</span>}>
                <span className="switch-container">
                  <input type="checkbox" className="switch is-rounded" id={guid}  onClick={this.on_changeMode} />
                  <label htmlFor={guid}>Deutsches Excelformat</label>
                </span>
              </Tooltip>
            ): ""
          } */}
          {
            this.props.usetabs == "true" ? (
              <div className="tabs is-boxed is-small is-left">
                <ul>
                  {
                    this.tabs.map((t,i) => (
                      <li key={i} className={this.state.currTab == i ? 'is-active' : ''}>
                        <div>
                          <Tooltip text={<span dangerouslySetInnerHTML={{__html: t.tooltip}}></span>}>
                            <a className="tab-link" onClick={(ev) => this.onTabChanged(i, ev)}>
                              <span>{t.label}</span>
                            </a>
                          </Tooltip>
                        </div>
                      </li>
                    ))
                  }
                </ul>
              </div>
            ) : ""
          }
          </div>
          <div className="right">
            {
              footerHtml != null ? <HTMLContent content={footerHtml}/> : ""
            }
            <span>
              <button className="button action-button is-small" onClick={this.on_copy_clicked}><FaCopy></FaCopy><span style={{marginLeft: "5px"}}>Kopieren</span></button>
            </span>
          </div>
        </div>
      </div>
    )
  }
}