import React from 'react'
import Navbar from '../Navbar/Navbar'
import '../../all.sass'
import '../../styles.sass'
import "../../extensions"
import { CookieWarning } from '../CookieWarning/CookieWarning';
import Footer from '../Footer/Footer';
import { NewsLetter } from '../Newsletter/Newsletter';
import { IHeaderProps, Header } from './Header';
import  { MyOutboundLink } from "../Misc";
import { AppInsightsService } from '../../services/AppInsightsService';
import { RouteTrackingService } from '../../services/RouteTrackingService';

const appInsights = AppInsightsService;
const routeTracking = RouteTrackingService;

export interface ILayoutProps 
{
  categoryBar?: any;
  header: IHeaderProps;
}

export class Flyer extends React.Component
{
  render()
  {
    return (
      <div className="flyer">
        <div className="flyer-bg">
        </div>
        <MyOutboundLink href="https://forms.gle/U6Fh6tLKddYWh3L18" rel="noopener nofollow" target="_blank" title="Hier gehts zum Quiz">
          <div className="flyer-content">
            <div>
              Unser <br/> Oster-Quiz
            </div>
          </div>
        </MyOutboundLink>
      </div>
    )
  }
}

export class Layout extends React.Component<ILayoutProps>
{
  componentDidMount()
  {
    if(typeof document !== "undefined")
    {
      let links = Array.from(document.querySelectorAll("a[mdOnClick]"));
      for(let l of links)
      {
        l.addEventListener("click", (ev) => {
          (window as any)[l.getAttribute("mdOnClick")]();
          ev.preventDefault();
        });
      }
    }
  }
  
  render()
  {
    return (
      <div className="root">
        {/* <Flyer></Flyer> */}
        <Header header={this.props.header}></Header>
        <NewsLetter/>
        <CookieWarning/>
        <Navbar />
        <div className="content-body-container">
          <div>{ this.props.children }</div>
          <Footer/>
        </div>
      </div>
    )
  }
}

export class ContentLayout extends React.Component<ILayoutProps> 
{
  render()
  {
    return (
      <Layout header={this.props.header}>
      
        <section className="section">
          <div className="container content" id="content-container">
            {/* {
              this.props.categoryBar != null ? this.props.categoryBar : ""
            } */}
            {this.props.children}
          </div>
        </section>
      </Layout>
    )
  }
}

export class ContentLayoutWithSidebar extends React.Component<ILayoutProps & {sidebar: any}>
{
  render()
  {
    return (
      <ContentLayout categoryBar={this.props.categoryBar} header={this.props.header}>
        <div className="columns">
          <div className="column is-7 is-offset-1">
            {this.props.children}
          </div>
          <div className="column is-3">
            {
              this.props.sidebar != null ? this.props.sidebar : ""
            }
          </div>
        </div>
      </ContentLayout>
    )
  }
}
