import { MyBehaviorSubject, IObservable } from '../common/MySubject';
import { Optional } from '../common/Types';
import * as ReactGA from "react-ga";
import { config } from '../../src_gatsby/config';

type strBool = "true"|"false"|null;

export interface ICookies
{
  cookieWarningDismissed2: strBool;
  enableGoogleAnalytics: strBool;
  enableAzureAppInsights: strBool;
  subscribed: strBool;
}

export class CookieServiceClass
{
  cookies: ICookies = {
    cookieWarningDismissed2: "false",
    subscribed: "false",
    enableGoogleAnalytics: "false",
    enableAzureAppInsights: "false"
  }
  
  private _cookiesChanged: MyBehaviorSubject<ICookies>;
  public $cookies: IObservable<ICookies>;
  
  constructor()
  {
    if(typeof window !== "undefined") //This prevents SSR from accessing document which is undefined during node build
    {
      this.cookies = this.getCookieObject();
      this._cookiesChanged = new MyBehaviorSubject(this.cookies);
      this.$cookies = this._cookiesChanged.asObservable();
      
      if(this.cookies.enableGoogleAnalytics == "true")
        this.initGa();
    }
  }
  
  initGa()
  {
    ReactGA.initialize(config.gaId);
    console.log("Initialized google analytics");
  }

  setCookie(name: keyof ICookies, value: string, emit: boolean = true)
  {
    document.cookie = `${name}=${value};max-age=${60*60*24*365}`;
    if(emit == true)
      this._cookiesChanged.emit(this.getCookieObject());
      
    if(name == "enableGoogleAnalytics" && value == "true")
    {
      if((window as any).ga == null)
        this.initGa();
        
      console.log("Enabled google analytics");
      window[`ga-disabled-${config.gaId}`] = false;
    }
    else if(name == "enableGoogleAnalytics" && value == "false" && (window as any).ga != null)
    {
      console.log("Disabled google analytics");
      window[`ga-disabled-${config.gaId}`] = true;
    }
    
  }
  
  setCookies(o: Optional<ICookies>)
  {
    for(let prop in o)
    {
      this.setCookie(prop as any, o[prop], false)
    }
    this._cookiesChanged.emit(this.getCookieObject());
  }

  private getCookieObject() : any
  {
    let cookieObj = {};
    if(document.cookie)
    {
      document.cookie.split(";")
        .map(c => c.split("=").map(e => e.trim()))
        .forEach(c => {
          cookieObj[c[0]] = c[1];
        });
    }
    return cookieObj;
  }
}

export const CookieService = new CookieServiceClass();
