import * as React from "react";
import { config } from "../../config";
import { postData, isMobile } from "../../tool";
import "./Search.sass";
import { FaSearch } from "react-icons/fa";
import { SearchService } from '../../services/SearchService';
import { AppInsightsService } from '../../services/AppInsightsService';

class State 
{
  query: string;
  suggestions: string[] = [];  
  suggestionStyle: any = {
    left: "",
    top: "",
    minWidth: ""
  };
}

interface SuggestResult
{
  suggest: {
    res: {
      text: string
      options: {
        text: string
      }[]
    }[];
  }
}

export class SearchInput extends React.Component<{}, State>
{
  inputRef: React.RefObject<HTMLInputElement> = React.createRef();
  searchAnchorRef: React.RefObject<HTMLAnchorElement> = React.createRef();
  containerRef: React.RefObject<HTMLDivElement> = React.createRef();
  
  constructor(props)
  {
    super(props);  
    this.state = new State();
    if(typeof window !== "undefined")
    {
      // window.addEventListener("blur", this.windowBlurHandler);
    }
  }
  
  clickHandler = (ev: MouseEvent) => {
    function isDescendant(parent, child) {
      let node = child.parentNode;
      while (node != null) {
          if (node == parent) {
              return true;
          }
          node = node.parentNode;
      }
      return false;
    }
    if(!isDescendant(this.containerRef.current, ev.target))
    {
      this.setState({
        suggestions: null
      })
    }
  }
  
  windowBlurHandler = () => {
    this.setState({
      suggestions: null
    });
  }
  
  handleChange = async (ev) => {
    let d = {};
    d[ev.target.name] = ev.target.value;
    this.setState(d);
    await this.getSuggestions(ev.target.value);
  }
  
  componentDidMount()
  {
    this.initPos()
  }
  
  componentWillUnmount()
  {
    window.removeEventListener("click", this.clickHandler);
    window.removeEventListener("blur", this.windowBlurHandler);
  }
  
  initPos = () => {
    let rect = this.inputRef.current.getBoundingClientRect();
    let style = {
      ...this.state.suggestionStyle
    }
    style.left = (this.inputRef.current.offsetLeft) + "px";
    style.top = (this.inputRef.current.offsetTop + rect.height) + "px";
    style.minWidth = rect.width + "px";
    style.width = rect.width + "px"
    this.setState({
      suggestionStyle: style
    });
  }
  
  async getSuggestions(query: string)
  {
    try
    {
      let result = await postData<SuggestResult>(config.elasticSearchUri + "/compl/_search?pretty=true", {
        _source: ["text"],
        suggest: {
          res: {
            prefix: query,
            completion: {
              field: "suggestions"
            }
          }
        }
      }, 
      {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Basic "+config.elsaticBasicAuth
        }
      });
      
      if(result && result.suggest && result.suggest.res)
      {
        this.setState({
          suggestions: result.suggest.res
            .selectMany(r => r.options)
            .map(o => o.text)
            .filter(r => r != query)
            .distinct()
        });
      }
    }
    catch(error)
    {
      AppInsightsService.trackError("Error fetching search suggestions", error);
    }
  }
  
  onSearchClicked = async () =>
  {
    this.searchAnchorRef.current.href = `/search?query=${this.inputRef.current.value}`
    this.searchAnchorRef.current.click();
    await SearchService.invokeChanged(this.inputRef.current.value);
    this.setState({
      suggestions: null
    })
  }
  
  onSuggestionClicked = async (s) => {
    this.inputRef.current.value = s
    this.setState({
      query: s
    });
    await this.onSearchClicked();
  }
  
  onKeyDown = async (ev: React.KeyboardEvent<HTMLInputElement>) => {
    if(ev.key == "Enter")
      await this.onSearchClicked();
  }
  
  render()
  {
    return (
      <div className="search-input" ref={this.containerRef}>
        <input onFocus={this.initPos} type="search" ref={this.inputRef} onChange={this.handleChange} onKeyDown={this.onKeyDown} placeholder="Artikelsuche" name="query" className="input" autoComplete="off"></input>
        {
          (this.state.suggestions != null && this.state.suggestions.length > 0) ? (
            <div style={this.state.suggestionStyle} className="suggestion-container">
              {
                this.state.suggestions.map((s,i) => (
                  <div className="suggestion" key={i}>
                    <a onClick={() => this.onSuggestionClicked(s)}>{s}</a>
                  </div>
                ))
              }
            </div>
          ) : ""
        }
        <button className="button search-button" onClick={this.onSearchClicked}><FaSearch></FaSearch></button>
        <a id="suggestion" href={`search?query=${this.state.query}`} style={{display: "none"}} ref={this.searchAnchorRef}></a>
      </div>
    )
  }
}
