import React from "react";
import { Link, StaticQuery, graphql } from "gatsby";
import "./Navbar.sass"
import Img from 'gatsby-image';
import { FaTwitter, FaPhone } from "react-icons/fa";
import  { MyOutboundLink } from "../Misc";
import { SearchInput } from "../Search/SearchInput";
import { config } from "../../config";


interface NavbarProps {
  logo: any,
}

interface NavbarState {
  active: boolean;
  navBarActiveClass: string;
}

class Navbar extends React.Component<NavbarProps, NavbarState> {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
      navBarActiveClass: ""
    };
  }

  toggleHamburger = () => {
    // toggle the active boolean in the state
    this.setState(
      {
        active: !this.state.active
      },
      // after state has been updated,
      () => {
        // set the class in state for the navbar accordingly
        this.state.active
          ? this.setState({
              navBarActiveClass: "is-active"
            })
          : this.setState({
              navBarActiveClass: ""
            });
      }
    );
  };

  render() {
    let navbarItems = config.navbarItems;
    return (
      <nav className="navbar is-transparent" role="navigation" aria-label="main-navigation">
        <div className="container">
          <div className="navbar-brand">
            <Link activeClassName="navbar-active" className=" navbar-item" to="/" title="Logo" >
              <Img fixed={this.props.logo.childImageSharp.fixed}></Img>
              <span className="logo-text">Durchblick durch Daten</span>
            </Link>
            {/* Hamburger menu */}
            <div className={`navbar-burger burger ${this.state.navBarActiveClass}`} data-target="navMenu" onClick={() => this.toggleHamburger()}>
              <span />
            </div>
          </div>
          <div id="navMenu" className={`navbar-menu ${this.state.navBarActiveClass}`}>
            <div className="navbar-start has-text-centered">
            {
              navbarItems.map((n,i) => {
                return (
                  <Link key={i} className="navbar-item" activeClassName="navbar-active" to={n.route}>
                    <span>{n.text}</span>
                  </Link>
                )
              })
            }
              <Link className="navbar-item" activeClassName="navbar-active" to="/kontakt">
                <FaPhone style={{marginRight: "5px"}} className="primary-col"/>
                <span>Kontakt</span>
              </Link>
            </div>
            <div className="navbar-end has-text-centered" >
              <SearchInput></SearchInput>
              {/* <MyOutboundLink className="navbar-item social" aria-label="Power Query Formatter" title="Power Query Formatter" href="https://powerqueryformatter.com" rel="noopener nofollow" target="_blank">
                <img style={{height: "25px"}} src="/powerquery-formatter-logo.png"/>
              </MyOutboundLink> */}
              <MyOutboundLink className="navbar-item social" aria-label="Twitter@ppvinsights" title="Twitter@ppvinsights" href="https://twitter.com/ppvinsights" rel="noopener nofollow" target="_blank">
                <FaTwitter/>
              </MyOutboundLink>
            </div>
          </div>
        </div>
      </nav>
    );
  }
};


export default () => (
  <StaticQuery
    query={graphql`
    {
      logo: file(
        absolutePath: { regex: "/.*\/data\/img\/*/"}  
        relativePath: { eq: "logo.png" }
        ) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
    `}
    render={(data) => <Navbar logo={data.logo} />}
  />
)


