import React from "react";
import { IAuthor } from "../../interfaces";
import "./ContentFooter.sass"
import { AuthorContent } from "../Author/AuthorContent";

export class AuthorFooter extends React.Component<{author: IAuthor, authorBadge: any, avatar: any}>
{
  render()
  {
    return (
      <div className="contentfooter-component author-footer">
        <AuthorContent badge={this.props.authorBadge} author={this.props.author} avatar={this.props.avatar}/>
      </div>
    )
  }
}

