import React from "react";
import { ICustomerStory, IGatsbyLocation } from '../../interfaces';
import { CustomerStory } from "../CustomerStory/CustomerStory";
import { randint } from "../../tool";

export class CustomerStoryFooter extends React.Component<{stories: ICustomerStory[], location: IGatsbyLocation}, {index: number}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      index: randint(0, this.props.stories.length)
    }
  }
  
  render() 
  {
    return (
      <div className="contentfooter-component">
        <hr/>
        <div>
          <h4> Kundenmeinungen </h4>
        </div>
        {
          this.props.stories.map((s,i) => (
            <div key={i} style={{display: i == this.state.index ? "block": "none"}}>
              <CustomerStory story={s} location={this.props.location}/>
            </div>
          ))
        }
      </div>
    )
  }
}
