import React from "react";
import "./Footer.sass";
import { StaticQuery, graphql } from "gatsby";
import  { MyOutboundLink } from "../Misc";
import { CookieService } from '../../services/CookieService';
import Img from 'gatsby-image';
import { Link as LinkNoPrefetch } from '@reach/router';

export class Footer extends React.Component<{icon: any, badge: any}>
{
  cookies_clicked = () => {
    CookieService.setCookie("cookieWarningDismissed2", "false");
  }
  render() 
  {
    return (
      <div className="footer-container">
        <div className="footer-link-container">
          <span>
            <LinkNoPrefetch to="/impressum">Impressum</LinkNoPrefetch>
          </span>
          <span>
            <LinkNoPrefetch to="/haftungsausschluss">Haftungsausschluss</LinkNoPrefetch>
          </span>
          <span>
            <LinkNoPrefetch to="/datenschutz">Datenschutz</LinkNoPrefetch>
          </span>
          {/* <span>
            <MyOutboundLink rel="noopener nofollow" target="_blank" href="https://powerqueryformatter.com">Power Query Formatter</MyOutboundLink>
          </span> */}
          <span>
            <button className="button is-small action-button" onClick={this.cookies_clicked}>Cookies anpassen</button>
          </span>
        </div>
        <div className="footer-icons">
          <div style={{marginRight: "40px"}}>
            <MyOutboundLink rel="noopener nofollow" target="_blank" href="https://bi-survey.com/powerbimap/">
              <Img fixed={this.props.badge.childImageSharp.fixed}>
              </Img>
            </MyOutboundLink>
          </div>
          <div className="footer-mglr">
            <span style={{marginRight: "5px", whiteSpace: "nowrap"}}>
              Powered by 
            </span>
            <MyOutboundLink rel="noopener nofollow" target="_blank" href="https://mogular.com">
              <Img fixed={this.props.icon.childImageSharp.fixed}>
              </Img>
            </MyOutboundLink>
          </div>
        </div>
      </div>
    )
  }
}


export default () => (
  <StaticQuery
    query={graphql`
    {
      icon: file(absolutePath: {regex: "/.*/data/img/mogularlogo/"}) {
        childImageSharp {
          fixed(width: 142) {
            ...GatsbyImageSharpFixed
          }
        }
      },
      badge: file(absolutePath: {regex: "/.*/data/img/powerbi-consultant-badge/"}) {
        childImageSharp {
          fixed(width: 142) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
    `}
    render={data => {
      return (
      <Footer icon={data.icon} badge={data.badge}>
      </Footer>
    )}
    }
  />
)