class SearchServiceClass
{
  handlers: ((q: string) => any)[] = [];
  
  constructor()
  {
    
  }
  
  registerHandler(fn: (q: string) => void|Promise<void>)
  {
    this.handlers.push(fn);
  }
  
  unregisterHandler(fn: (q: string) => void|Promise<void>)
  {
    this.handlers.remove(fn); 
  }
  
  async invokeChanged(q: string)
  {
    for(let h of this.handlers)
    {
      let res = h(q);
      if(res.then != null)
        await res;
    };
  }
}


export const SearchService = new SearchServiceClass();
