import React from "react";
import "./Misc.sass";

export class Tooltip extends React.Component<{text: any}, {hovered: boolean, style: any}>
{
  ref = React.createRef() as React.RefObject<HTMLDivElement>;
  timeout = null;
  
  constructor(props)
  {
    super(props);
    this.state = {
      hovered: false,
      style: {
        transform: "",
        bottom: "0px",
        left: "0px"
      }
    }
  }
  
  componentDidMount()
  {
    this.ref.current.addEventListener("mouseenter", this.onMouseEnter);
    this.ref.current.addEventListener("mouseleave", this.onMouseLeave);
  }
  
  componentWillUnmount()
  {
    this.ref.current.removeEventListener("mouseenter", this.onMouseEnter);
    this.ref.current.removeEventListener("mouseleave", this.onMouseLeave);
    if(this.timeout)
      clearTimeout(this.timeout);
  }
  
  onMouseEnter = (ev) => {
    if(this.timeout)
      clearTimeout(this.timeout);
      
    let rect = this.ref.current.getBoundingClientRect();
    this.setState({
      hovered: true,
      style: {
        top: rect.height + 5 + "px",
        left: "0px"
      }
    });
  }
  
  onMouseLeave = (ev) => {
    this.timeout = setTimeout(() => {
      this.setState({
        hovered: false
      });
    }, 200)
  }
  
  render()
  {
    return (
      <div style={{position: "relative"}}>
        <div ref={this.ref}>
          {this.props.children}
          <div style={this.state.style} className={`tooltip ${this.state.hovered == true ? "": "no-display"}`}>
            <div className="tooltip-arrow"></div>
            <div className="tooltip-text">
              {
                this.props.text
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}