import * as React from "react";
import { OutboundLink } from 'react-ga';

interface IProps
{
  href: string;
  eventLabel?: string;
  target?: string;
  rel?: string;
}

export class MyOutboundLink extends React.Component<IProps & React.HTMLProps<HTMLAnchorElement>>
{
  readonly defaultEventLabel = "outboundLink";
  readonly defaultTarget = "_blank";
  readonly defaultRel = "nofollow";
  
  render()
  {
    return (
      <OutboundLink to={this.props.href} eventLabel={this.props.eventLabel ?? this.defaultEventLabel} target={this.props.target ?? this.defaultTarget} rel={this.props.rel ?? this.defaultRel} className={this.props.className} id={this.props.id} style={this.props.style}>
        {
          this.props.children
        }
      </OutboundLink>
    )
  }
}