import { globalHistory } from '@reach/router'
import { urlJoin } from '../tool';

class RouteTrackingServiceClass
{
  trackingRouteBase = "/_track";
  sub;
  constructor()
  {
    this.sub = globalHistory.listen(async (ev) => {
      let path = urlJoin(this.trackingRouteBase, ev.location.pathname);
      if(path.endsWith("/") == false)
        path = path + "/";
      
      await fetch(path, {
        method: "GET",
        cache: "no-cache"
      });
    })
  }
}

export const RouteTrackingService = new RouteTrackingServiceClass();